export default ".weather{position:relative;max-width:400px;margin:0 auto;display:flex;flex-direction:column;justify-content:center;box-sizing:border-box;font-family:\"Ubuntu\",sans-serif;font-size:14px}.weather-header{display:flex;justify-content:space-between;align-items:center;margin-bottom:20px;background-color:#fff}.weather-header__title{font-size:24px}.weather-list{position:relative;min-height:400px}.weather-item{border:1px solid #c2baba;border-radius:16px;padding:20px}.weather-item:not(:first-child){margin-top:16px}.weather-item__name{font-size:24px;font-weight:bold}.weather-item__visualization{display:flex;align-items:center}.weather-item__visualization-icon{width:100px;height:100px}.weather-item__visualization-temp{font-size:26px;font-weight:bold}.weather-item__desc{font-size:16px}.weather-item__options{margin-top:16px;display:flex;flex-wrap:wrap}.weather-item__option-item{width:50%;margin-bottom:8px}.weather-settings-from{margin-top:20px;display:flex;justify-content:space-between;align-items:flex-end}.weather-settings-from__field{position:relative;width:calc(100% - 40px);display:flex;flex-direction:column}.weather-settings-from__label{font-size:20px;margin-bottom:4px}.weather-settings-from__input{font-size:16px;line-height:26px;box-sizing:border-box}.weather-settings-from__error{position:absolute;bottom:-18px;left:0;color:#9d0000;font-size:12px}.weather-locations-item{display:flex;align-items:center;padding:8px;background-color:#f5f5f5;border-radius:5px;transition:.3s ease-in-out background-color,.3s ease-in-out transform;-webkit-user-select:none;-moz-user-select:none;user-select:none}.weather-locations-item:hover{background-color:#ebebeb}.weather-locations-item:not(:first-child){margin-top:12px}.weather-locations-item.is-active{opacity:.2}.weather-locations-item.is-over{transform:scale(1.08)}.weather-locations-item__title{margin-left:14px;margin-right:auto}.weather-access{position:absolute;top:100px;left:0;width:100%}.weather-access__notify{padding:14px 20px;border-radius:8px;background-color:#ffa9a9}.weather-access__notify-text{font-size:14px;line-height:20px;text-align:center;margin-bottom:10px}.weather-access__notify-btn{margin:0 auto}.the-button{display:flex;position:relative;padding:0;border:none;font:inherit;color:inherit;background-color:rgba(0,0,0,0);cursor:pointer}.the-button:after{content:\"\";position:absolute;top:-1px;left:-1px;width:calc(100% + 2px);height:calc(100% + 2px);background-color:rgba(221,221,221,.33);border-radius:10px;transform:scale(0);transition:.3s ease-in-out transform}.the-button:hover:after{transform:scale(1)}.the-button__icon{-o-object-fit:contain;object-fit:contain}.the-button--sm .the-button__icon{width:32px;height:32px}.the-button--md .the-button__icon{width:40px;height:40px}.the-button--lg .the-button__icon{width:50px;height:50px}.the-button--move{cursor:move}.the-button--move:before{content:\"\";position:absolute;top:-7px;left:-7px;right:-7px;bottom:-7px}.the-button--loading{pointer-events:none}.the-button--loading .the-button__icon{opacity:0}.the-button--text{padding:4px 10px;font-size:16px;line-height:20px;background-color:#b0a5a5;border-radius:8px}@-webkit-keyframes loading{to{transform:rotate(360deg)}}@keyframes loading{to{transform:rotate(360deg)}}.the-spinner{position:absolute;top:0;left:0;right:0;bottom:0;margin:auto;width:40px;height:40px;border-radius:50%;background:rgba(0,0,0,0);border-top:4px solid #fff;border-right:4px solid #fff;border-bottom:4px solid #777;border-left:4px solid #777;-webkit-animation:loading 1s infinite linear;animation:loading 1s infinite linear}.the-spinner--lg{width:60px;height:60px}.the-spinner--sm{width:20px;height:20px;border-width:2px}"